@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
}
body {
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  position: relative;
  min-width: 320px;
}
.main-header__logo-title-image {
  margin-left: 60px;
  vertical-align: super;
}
@media (min-width: 768px) {
  .main-header__logo-title-image {
    display: none;
  }
}
.main-container {
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
.main-container__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 41px;
  padding: 27px 20px 0 20px;
  font-size: 36px;
  line-height: 36px;
  min-width: 320px;
}
@media (min-width: 1440px) {
  .main-container__title {
    padding-top: 120px;
  }
}
.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 33px;
  padding: 10px 20px;
  padding-bottom: 17px;
  z-index: 1;
  box-sizing: border-box;
}
@media (min-width: 1440px) {
  .main-nav {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    gap: 33px;
  }
}
.main-nav__item-link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 1440px) {
  .main-nav__item-link {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__item-link--white {
    color: #ffffff;
  }
}
.button {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  background-color: #68b738;
  text-align: center;
  text-transform: uppercase;
  border: none;
  text-decoration: none;
  cursor: pointer;
  padding-top: 11px;
  padding-bottom: 11px;
}
.intro {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  box-sizing: border-box;
}
@media (min-width: 768px) and (max-width: 1440px) {
  .intro::before,
  .intro::after {
    display: none;
  }
}
.intro::before {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 302px;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: -1;
}
.intro::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  width: 100%;
  height: 302px;
  background-image: url("../img/background-cat-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: inherit;
  background-color: #444444;
  z-index: -2;
}
@media (min-width: 1440px) {
  .intro::before {
    top: 0;
    right: 0;
    width: 50%;
    height: 696px;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: -1;
  }
  .intro::after {
    top: 0;
    right: -50%;
    width: 50%;
    height: 696px;
    background-image: url("../img/background-cat-desktop.jpg");
    background-repeat: no-repeat;
    background-position: right top;
    background-color: #444444;
    z-index: -2;
  }
}
.intro__wrapper {
  margin-left: 20px;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .intro__wrapper {
    min-height: 750px;
  }
}
.intro__title {
  color: #ffffff;
  padding-top: 27px;
  text-align: center;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin-bottom: 25px;
  margin-top: 0;
}
@media (min-width: 768px) {
  .intro__title {
    color: #000000;
    text-align: start;
  }
}
.intro__slogan {
  margin-top: 0;
  margin-bottom: 28px;
  line-height: 14px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .intro__slogan {
    color: #000000;
    text-align: start;
  }
}
.intro__img {
  display: block;
  margin: 0 auto;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .intro__img {
    position: absolute;
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .intro__img {
    position: absolute;
    top: 140px;
    left: 40%;
  }
}
@media (min-width: 768px) {
  .intro__link {
    display: inline-block;
    padding: 15px 26px;
  }
}
@media (min-width: 1440px) {
  .intro__text {
    padding-top: 140px;
  }
}
.programs__list {
